.App {
  text-align: center;
  font-family: Arial, sans-serif;
  border-radius: 10px;
}

body {
    background-image: url('./images/BlankHotPocket.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
}

h1 {
    color: #333;
}

h2 {
    color: #555;
}

.card-container {
    display: flex;
    justify-content: space-around;
    gap: 20px;
    margin-top: wrap;
}

.card {
    margin: 10px;
    width: 200px;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.2s ease;
}

.card:hover {
    background-color: #dcdcdc;
    transform: scale(1.05);
}

button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border: none;
    background-color: #4CAF50;
    color: white;
    border-radius: 5px;
}

ul {
    list-style-type: none;
    padding: 0;
}

.restart-button {
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    background-color: #ff6f61;
    color: white;
    cursor: pointer;
    border-radius: 5px;
    margin-top: 20px;
}

.restart-button:hover {
    background-color: #ff4f41;
}

@media (max-width: 600px) {
    .card-container {
        flex-direction: column;
        align-items: center;
    }

    .card {
        width: 90%;
        margin: 10px 0;
    }

    button {
        width: 90%;
    }

    .restart-button {
        padding: 8px 16px;
        font-size: 14px;
        width: 100%;
        margin-top: 15px;
    }
}

